.score-body-section {
    display: flex;
    flex-direction: column;
    width: 100%;
    min-height: 100%;
    align-items: center;
    padding-top: 7rem;
  }
  
  .score-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-evenly;
    font-family: Arial;
    font-size: 2rem;
    font-weight: normal;
    font-stretch: normal;
    font-style: normal;
    line-height: 1.21;
    letter-spacing: normal;
    color: #fff;
  }
  
  .score-heading {
    font-size: 2rem;
    font-weight: 500;
    line-height: 1.2;
  }
  
  .score-count {
    font-size: 5rem;
    font-weight: 500;
    line-height: 1.2;
  }
  
  .high-score-text {
    font-weight: normal;
    font-size: 1.5rem;
    position: relative;
  }
  .end-game-button {
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: transparent;
    border: none;
    outline: none;
    padding: 1rem;
    cursor: pointer;
    text-shadow: 0 0 16px rgba(0, 0, 0, 0.16);
    font-family: Arial;
    font-size: 40px;
    font-weight: 500;
    font-stretch: normal;
    font-style: normal;
    line-height: 1.2;
    letter-spacing: normal;
    color: #ff5155;
  }
  
  .reload-image {
    padding-right: 1.5rem;
    width: 50px;
    height: 50px;
    object-fit: contain;
  }
  
  .quit-game-container {
    width: 100%;
    min-height: 7rem;
  }
  
  .quit-game-button {
    display: flex;
    background-color: transparent;
    border: none;
    outline: none;
    font-family: Arial;
    font-size: 35px;
    font-weight: 500;
    cursor: pointer;
    font-stretch: normal;
    font-style: normal;
    line-height: 1.19;
    letter-spacing: normal;
    text-align: left;
    color: #ff5155;
    padding: 10px;
    padding-top: 7rem;
  }